import React from 'react';
import emailjs from 'emailjs-com';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from "prop-types";
import './contact-form.styles.css';

const styles = theme => ({
    root: {
        '& label.Mui-focused': {
            color: '#d72b90',
        },
        '& .MuiInput-underline:after': {
            borderBottomColor: '#d72b90',
          }
    },
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      marginLeft: theme.spacing.unit,
      marginRight: theme.spacing.unit,
      width: 200,
    },
  
    cssLabel: {
      color: 'white'
    },
    input: {
        color: 'white'
    },
  
    cssOutlinedInput: {
      '&$cssFocused $notchedOutline': {
        borderColor: 'white',
      }
    },
  
    cssFocused: {},
  
    notchedOutline: {
      borderWidth: '1px',
      borderColor: 'white !important'
    },
  
  });

class ContactForm extends React.Component {
    constructor() {
        super();

        this.state = {
            email: '',
            name: '',
            subject: '',
            message: '',
        }
    }

    handleSubmit(e) {
        //preventing default behavior of forms
        e.preventDefault();

        //destructuring properties off of state
        const { email, name, subject, message } = this.state;

        //email template parameters
        let templateParams = {
            from_name: name + email,
            subject: subject,
            message_html: message
        }

        emailjs.send(
            'contactus',
            'template_v5ceg50h',
            templateParams,
            'user_DuU2XeEB5hlTyRLmqzMS5'
        )
        .then((res) => {
            console.log('SUCCESS', res.status, res.text);
        }).then((error) => {
            console.log('FAILED...Error: ', error)
        })

        this.resetForm();
        
    }

    resetForm() {
        //after form is submitting in handleSubmit, this function is called to clear inputs in state
        this.setState({
            name: '',
            email: '',
            subject: '',
            message: ''
        });
    }

    handleChange = (param, e) => {
        this.setState({ [param]: e.target.value });
    }

    render() {
        const { classes } = this.props;
        return (
            <>

                <Grid container>
                    <Grid item xs={false} sm={4} md={6}>
                        <br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
                        <Typography 
                            variant="h1" 
                            className='header'
                        >
                        Are you ready to get <span className='pink'>Discovered</span>?
                        </Typography>
                        <Typography variant="body1" className='intro'>
                            We value music and we value you. Please fill out the contact form
                            with your information. Please include the following in the message body:
                            band name, genre, band member(s), links to social media (Facebook, Twitter, etc.) and lastly,
                            links to your music!
                        </Typography>
                    </Grid>
                    <Grid item xs={false} sm={8} md={6} >
                        <Typography variant="h5" component="h1">Hello</Typography>
                            <div className='form-control'>
                                <form onSubmit={this.handleSubmit.bind(this)} className={classes.root}>
                            
                                    <TextField 
                                        id="outlined-basic" 
                                        label="Email Address" 
                                        variant="outlined" 
                                        type="email"
                                        name="email"
                                        fullWidth
                                        margin="normal"
                                        onChange={this.handleChange.bind(this, 'email')}
                                        InputLabelProps={{
                                            classes: {
                                              root: classes.cssLabel,
                                              focused: classes.cssFocused,
                                              className: classes.input
                                            },
                                        }}
                                        InputProps={{
                                            classes: {
                                              root: classes.cssOutlinedInput,
                                              focused: classes.cssFocused,
                                              notchedOutline: classes.notchedOutline,
                                              className: classes.multilineColor
                                            },
                                            inputMode: "numeric"
                                        }}
                                    />
                                
                                
                                    <TextField 
                                        id="outlined-basic"
                                        label="Name"
                                        variant="outlined"
                                        type="text"
                                        name="name"
                                        fullWidth
                                        margin="normal"
                                        value={this.state.name}
                                        onChange={this.handleChange.bind(this, 'name')}
                                        InputLabelProps={{
                                            classes: {
                                              root: classes.cssLabel,
                                              focused: classes.cssFocused,
                                            },
                                        }}
                                        InputProps={{
                                            classes: {
                                              root: classes.cssOutlinedInput,
                                              focused: classes.cssFocused,
                                              notchedOutline: classes.notchedOutline,
                                            },
                                            inputMode: "numeric"
                                        }}
                                    />
                                
                                
                                    <TextField 
                                        id="outlined-basic"
                                        label="Subject"
                                        variant="outlined"
                                        type="text"
                                        fullWidth
                                        margin="normal"
                                        name="subject"
                                        value={this.state.subject}
                                        onChange={this.handleChange.bind(this, 'subject')}
                                        InputLabelProps={{
                                            classes: {
                                              root: classes.cssLabel,
                                              focused: classes.cssFocused,
                                            },
                                        }}
                                        InputProps={{
                                            classes: {
                                              root: classes.cssOutlinedInput,
                                              focused: classes.cssFocused,
                                              notchedOutline: classes.notchedOutline,
                                            },
                                            inputMode: "numeric"
                                        }}
                                    />
                                
                                
                                    <TextField 
                                        id="outlined-basic"
                                        label="Message"
                                        multiline
                                        rows="4"
                                        type="text"
                                        fullWidth
                                        margin="normal"
                                        name="message"
                                        value={this.state.message}
                                        onChange={this.handleChange.bind(this, 'message')}
                                        InputLabelProps={{
                                            classes: {
                                              root: classes.cssLabel,
                                              focused: classes.cssFocused,
                                            },
                                        }}
                                        InputProps={{
                                            classes: {
                                              root: classes.cssOutlinedInput,
                                              focused: classes.cssFocused,
                                              notchedOutline: classes.notchedOutline,
                                            },
                                            inputMode: "numeric"
                                        }}
                                    />
                                <Button variant="contained" color="secondary" type="submit">Submit</Button>
                            </form>
                        </div>
                    </Grid>
                </Grid>
                
            </>
        )
    }
}

ContactForm.propTypes = {
    classes: PropTypes.object.isRequired
}
  
export default withStyles(styles)(ContactForm);

