import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Slider from 'react-slick';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import image1 from '../../constants/images/img1.jpg';
import image4 from '../../constants/images/img4.JPG';
import image3 from '../../constants/images/img3.jpg';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import './image-slider.styles.css';

const useStyles = makeStyles({
    card: {
        padding: 50,
        backgroundColor: '#222423'
    }
})

const ImageSlider = () => {
    
    const classes = useStyles();
    const settings = {
        dots: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoPlay: true,
        arrows: false
    }

    return (
        <div>
                <Grid container>
                    <Grid item xs={12} sm={12} md={12}>
                        <Card className={classes.card}>
                            <CardContent>
                                <Slider {...settings}>
                                    <div>
                                        <img src={image1} alt="test"  />
                                    </div>
                                    <div>
                                        <img src={image4} alt="test" />
                                    </div>
                                    <div>
                                        <img src={image3} alt="test" />
                                    </div>
                                </Slider>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
        </div>
    )
}

export default ImageSlider;